import { API } from './../api';
import { resultsByScores } from './results-by-scores';

const storageKey = 'qa-conf-game__settings';

/**
 * @typedef SettingsState
 * @property {object} config
 * @property {string} adminPassword Пароль для администрирования приложения.
 * @property {number} appContainerWidth Ширина главного блока приложения.
 * @property {object} resultsByScores Тексты и стикеры результатов пользователя.
 */


class Settings {

  constructor() {
    this._appContainerWidth = '1500px';
    this._resultsByScores = resultsByScores;
  }


  getAppContainerWidth() {
    return this._appContainerWidth;
  }


  /**
   * @returns {SettingsState}
   */
  getSettingsForExport() {
    return {
      config: {
        adminPassword: API.getAdminPassword(),
        appContainerWidth: this.getAppContainerWidth(),
        resultsByScores: this._resultsByScores
      }
    };
  }


  /**
   * @param {SettingsState} incoming
   */
  setSettingsByImport(incoming) {
    if (!incoming) {
      return;
    }
    this.setSettings(incoming);
  }


  setSettings(incoming) {
    this.importConfig(incoming.config);
    // Сохраняем актуальные настройки после мерджа!
    this.storeToStorage();
  }


  importConfig(config) {
    if (!config) {
      return;
    }

    const adminPassword = config.adminPassword;
    if (adminPassword) {
      API.setAdminPassword(adminPassword);
    }

    const appContainerWidth = config.appContainerWidth;
    if (appContainerWidth) {
      this._appContainerWidth = appContainerWidth;
    }

    const _resultsByScores = config.resultsByScores;
    if (_resultsByScores) {
      this._resultsByScores = _resultsByScores;
    }
  }


  getResultDataByFinalState(finalState) {
    return this._resultsByScores[finalState];
  }


  storeToStorage() {
    const current = this.getSettingsForExport();
    const serialized = JSON.stringify(current);
    localStorage.setItem(storageKey, serialized);
  }


  restoreFromStorage() {
    const serialized = localStorage.getItem(storageKey);
    if (!serialized) {
      console.log('SETTINGS', 'no settings were stored in local storage.');
      return;
    }
    let json;
    try {
      json = JSON.parse(serialized);
    } catch (e) {
      console.log('SETTINGS', 'error while restoring settings from local storage', e);
      return;
    }
    this.setSettings(json);
    console.log('SETTINGS', 'settings were successfully restored from local storage!');
    console.log('SETTINGS', json);
  }


  clearSettings() {
    localStorage.removeItem(storageKey);
  }

}


export const settings = new Settings();



